/* Иконки */
.icoTab {
  display: flex;
  height: 24px;
  background-color: white;
  background-repeat: no-repeat;
  background-size: cover;
  width: 24px;
  align-self: center;
  border: 0.5px solid #979797;
  border-radius: 100%;
  margin-left: -1px;
}
.icoTab#vape {
  background-image: url("../../../../../public/img/main/tabIcon/vape.png");
}
.icoTab#hookah {
  background-image: url("../../../../../public/img/main/tabIcon/hookah.png");
}
.icoTab#snus {
  background-image: url("../../../../../public/img/main/tabIcon/snus.png");
}

/* главный контейнер*/

.tabs__nav .global {
  background-color: white;
  display: flex;
  width: 80%;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  padding-left: 10%;
}

.ulCategory{
  display: flex;
  align-items: center;
  justify-content: start;
  flex-flow: row nowrap;
  overflow-y: hidden;
  background-color: white;
  padding-left: 10%;
}
.ulCategory::-webkit-scrollbar {
  display: none;
}
._unactive{
    display: none;
}

.liCategory {
  height: 52px;
  white-space: nowrap;
  background-color: white;
  outline: none;
  border: none;
  color: #67696D;
  cursor: pointer;
}
.liCategory_active{
    cursor: default;
    color: #2459FF;
}
.tabs__nav {
  margin: auto;
  margin-top: 25px;
  position: sticky;
  top: 89px;
}

.line{
    border-bottom: 1px solid rgba(151, 151, 151, 0.2);
}