.navGlobal {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 10px 17px 10px 12px;
  min-width: 357px;
  width: 80%;
  height: 57px;
  background: #ffffff;
  box-shadow: 0px 1px 12px 4px rgba(0, 0, 0, 0.1);
  border-radius: 1000px;
  position: sticky;
  bottom: 10px;
  align-self: center;
  justify-content: space-around;
}


.buttonNav {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 4px 8px;
  gap: 4px;
  width: 82px;
  height: 37px;
  display: flex;
  border: none;
  cursor: pointer;
  color: #000000;
  background: #ffffff;
  border-radius: 1000px;
}
.buttonNav:hover:not(.buttonNav_active){
    background-color: #8ca9ff;
    color:#ffffff;
}
.buttonNav_active {
    cursor: default;
    color: #ffffff;
    background: #2459ff;
}

.icoSize {
    font-size: 20.5px;
}