.tabs__btn {
  cursor: pointer;
  min-width: 114px;
  height: 37px;
  border: 0.5px solid #979797;
  border-radius: 23.5px;
  justify-content: start;
  gap: 4px;
  align-items:center;
  display: flex;
  background-color: white;
}

.tabs__btn_active {
  color: white;
  background-color: #2459FF;
  cursor: default;
}
